.floating {
    &-banner {
        position: fixed;
        isolation: isolate;
        bottom: 100px;
        right: 77px;
        background-color: $color-white;
        width: 170px;
        height: 150px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        text-align: center;
        text-decoration: none;
        transition: opacity 0.35s ease-in-out;
        z-index: 90;

        @include mq("desktop", "max") {
            right: 20px;
        }

        @include mq("bootstrap-lg", "max") {
            width: 130px;
            height: 110px;
            bottom: 30px;
        }

        @include mq("phablet", "max") {
            width: 100%;
            height: 50px;
            bottom: 0;
            right: 0;
            border-radius: 0;
            flex-direction: row;
            justify-content: space-between;
            padding: 14px 20px 10px;
        }

        &::after {
            content: "";
            position: absolute;
            inset: 11px;
            background-image: url(../img/icons/banner-icon.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            z-index: -1;

            @include mq("phablet", "max") {
                inset: 4px 0;
                background-repeat: repeat-x;
            }
        }

        span {
            &:first-of-type {
                color: $color-black;
                font-size: 2.126rem;
                font-weight: 700;

                @include mq("bootstrap-lg", "max") {
                    font-size: 1.6rem;
                }

                br {
                    @include mq("phablet", "max") {
                        display: none;
                    }
                }
            }

            &:last-of-type {
                color: $color-green;
                font-size: 3.402rem;
                font-weight: 600;
                font-family: $font-sec;
                @include mq("bootstrap-lg", "max") {
                    font-size: 2.4rem;
                }
            }
        }

        &:hover {
            opacity: 0.7;
        }
    }
}