.nav {
    transition: all 0.35s ease-in-out;

    @include mq("tablet", "max") {
        opacity: 0;
        visibility: hidden;
        background-color: $color-white;
        padding-top: 77px;
        width: 100%;
        // height: calc(100vh - 77px);
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: -1;
        transition: all 0.35s ease-in-out;
    }

    &.is-opened {
        opacity: 1;
        visibility: visible;
    }

    &__wrapper {
        display: flex;
        align-items: center;
        gap: 32px;

        @include mq("desktop", "max") {
            padding-top: 10px;
            gap: 20px;
        }

        @include mq("tablet", "max") {
            padding: 40px 20px;
            display: block;
            height: 100%;
            overflow: auto;
        }
    }

    &-list {
        display: flex;
        align-items: center;
        gap: 28px;

        @include mq("bootstrap-lg", "max") {
            gap: 16px;
        }

        @include mq("tablet", "max") {
            display: block;
            margin-bottom: 30px;
        }
    }

    &-item {
        @include mq("tablet", "max") {
            text-align: center;
        }

        &:not(:last-of-type) {
            @include mq("tablet", "max") {
                margin-bottom: 40px;
            }
        }
    }

    &-link {
        text-decoration: none;
        color: $color-black;
        font-size: 2.4rem;
        font-weight: 600;
        letter-spacing: 0.03em;
        transition: color 0.35s ease-in-out;

        @include mq("desktop", "max") {
            font-size: 2rem;
        }

        @include mq("bootstrap-lg", "max") {
            font-size: 1.6rem;
        }

        &:hover {
            color: $color-green;
        }
    }

    &-social {
        text-align: center;

        &__link {
            display: block;
            padding-bottom: 4px;
            transition: opacity 0.35s ease-in-out;

            @include mq("tablet", "max") {
                display: inline-block;
            }

            &:hover {
                opacity: 0.7;
            }
        }
    }
}