.pagination {
    list-style-type: none;
    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        gap: 22px;
        @include mq("tablet", "max") {
            gap: 15px;
        }
    }
    li {
        list-style-type: none;
    }
    &__item {
        a {
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 2rem;
            font-weight: 400;
            line-height: 1;
            color: $color-black;
            text-decoration: none;
            position: relative;
            padding-bottom: 2px;
            transition: all 0.35s ease-in-out;
            @include mq("tablet", "max") {
                font-size: 1.8rem;
            }
            &:hover {
                opacity: 0.7;
                &::after {
                    background-color: $color-black;
                }
                @include mq("tablet", "max") {
                    opacity: 1;
                }
            }
            &::after {
                content: "";
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                bottom: 0;
                background-color: transparent;
                width: 12px;
                height: 1px;
                transition: all 0.35s ease-in-out;
            }
        }

        &.-dots {
            a {
                &::after {
                    content: none;
                }
                &:hover {
                    opacity: 1;
                }
            }
        }

        &.-active {
            a {
                &::after {
                    background-color: $color-black;
                }
            }
        }

        &.-prev,
        &.-next {
            a {
                border-bottom: none;
                padding-bottom: 0;
                &:after {
                    content: none;
                }
            }
        }
        &.-prev {
            padding-right: 10px;
            @include mq("tablet", "max") {
                padding-right: 5px;
            }
        }
        &.-next {
            padding-left: 10px;
            @include mq("tablet", "max") {
                padding-left: 5px;
            }
        }
    }
}

.navigation {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 35px;
    a {
        font-size: 1.4rem;
        font-weight: 400;
        font-family: $font-sec;
        color: $color-black;
        text-decoration: none;
        transition: all 0.35s ease-in-out;
        position: relative;
        &:hover {
            opacity: 0.7;
        }
        @include mq("tablet", "max") {
            opacity: 1;
        }
    }

    .navigation__lft,
    .navigation__rgt {
        a {
            display: flex;
            align-items: baseline;
            gap: 22px;
            @include mq("tablet","max"){
                gap: 15px;
            }
        }
    }
    .navigation__lft {
        a {
            &::before {
                content: "";
                background-color: $color-black;
                mask-repeat: no-repeat;
                mask-size: cover;
                width: 6px;
                height: 7px;
                display: block;
                mask-image: url(../img/icons/icon-arrow-left.svg);
            }
        }
    }
    .navigation__rgt {
        a {
            &::after {
                content: "";
                background-color: $color-black;
                mask-repeat: no-repeat;
                mask-size: cover;
                width: 6px;
                height: 7px;
                display: block;
                mask-image: url(../img/icons/icon-arrow-right.svg);
            }
        }
    }
}
