.footer {
    padding: 36px 0 90px;

    &__part {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 33px;

        @include mq("tablet", "max") {
            gap: 15px;
        }

        @include mq("phablet", "max") {
            gap: 10px;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
        }
    }

    &__item {
        position: relative;

        &:not(:last-of-type)::after {
            position: absolute;
            content: "";
            width: 1px;
            height: 16px;
            top: 50%;
            right: -9%;
            transform: translate(-50%, -50%);
            background-color: $color-black;

            @include mq("tablet", "max") {
                right: -6%;
            }

            @include mq("phablet", "max") {
                display: none;
            }
        }

        &-link {
            text-decoration: none;
            font-size: 1.6rem;
            line-height: 2.6;
            letter-spacing: 0.03em;
            font-weight: 600;
            color: $color-black;
            transition: all 0.35s ease-in-out;
            cursor: pointer;

            @include mq("tablet", "max") {
                font-size: 1.4rem;
            }

            &.-social {
                padding-bottom: 6px;
                display: block;
            }

            &:hover {
                opacity: 0.7;
            }
        }
    }

    &__image {
        cursor: pointer;
        transition: all 0.35s ease-in-out;

        &:hover {
            opacity: 0.7;
        }
    }

    &__text {
        font-size: 1.6rem;
        line-height: 2.6;
        letter-spacing: 0.03em;
        color: $color-black;
        text-align: center;

        @include mq("phablet", "max") {
            font-size: 1.4rem;
            text-align: left;
            padding-top: 10px;
        }
    }

    &__logo {
        text-align: center;
        padding-top: 50px;

        @include mq("phablet", "max") {
            text-align: left;
            padding-top: 30px;
        }
    }

    &__pic {
        cursor: pointer;
        transition: all 0.35s ease-in-out;

        &:hover {
            opacity: 0.7;
        }
    }
}