.about {
    &__wrapper {
        padding-top: 90px;
    }

    &__column {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding-bottom: 80px;
        gap: 37px;

        @include mq("bootstrap-lg", "max") {
            gap: 20px;
        }

        @include mq("tablet", "max") {
            gap: 20px;
        }

        @include mq("phablet", "max") {
            flex-direction: column;
            width: 100%;
            gap: 30px;
            padding-bottom: 50px;
        }
    }

    &__left {
        flex: 0 0 calc((100% - 48.6%) - 37px);
        // width: 48.6%;
        margin-top: 40px;

        @include mq("tablet", "max") {
            width: 100%;
            margin-top: 10px;
        }

    }

    &__point {
        font-size: 3.4rem;
        font-weight: 700;
        letter-spacing: -0.03em;
        padding: 0 0 30px;

        @include mq("bootstrap-lg", "max") {
            font-size: 3rem;
        }

        @include mq("tablet", "max") {
            font-size: 2.5rem;
            padding: 0 0 20px;
        }
    }

    &__title {
        font-family: $font-sec;
        font-size: 2.83rem;
        line-height: 1.79;
        font-weight: 900;
        letter-spacing: -0.03em;
        color: $color-black;

        &:not(:last-child) {
            padding-bottom: 20px;

            @include mq("bootstrap-lg", "max") {
                padding-bottom: 15px;
            }
        }

        @include mq("bootstrap-lg", "max") {
            font-size: 2.2rem;
        }

        @include mq("tablet", "max") {
            font-size: 2rem;
        }
    }

    &__desc {
        font-size: 1.8rem;
        font-weight: 600;
        line-height: 1.89;
        letter-spacing: -0.03em;
        color: $color-black;
        padding-top: 5px;
        // padding-right: 37px;
        padding-right: 25px;

        @include mq("desktop-lg", "max") {
            padding-right: 37px;
        }

        @include mq("bootstrap-lg", "max") {
            padding-right: 0;
            font-size: 1.6rem;
        }

        @include mq("tablet", "max") {
            font-size: 1.5rem;
        }
    }

    &__right {
        flex: 0 0 48.6%;
        text-align: center;

        @include mq("tablet", "max") {
            width: 100%;
            height: 100%;
        }

        &-title {
            font-size: 5rem;
            font-weight: 700;
            line-height: 1.05;
            color: $color-white;
            text-align: center;

            @include mq("bootstrap-lg", "max") {
                font-size: 3rem;
            }

            @include mq("tablet", "max") {
                font-size: 2.5rem;
            }
        }
    }

    .-reverse {
        flex-direction: row-reverse;

        @include mq("phablet", "max") {
            flex-direction: column;
            width: 100%;
        }
    }
}