.tabs {
    &-nav {
        &.-mb {
            margin-bottom: 60px;
            @include mq("tablet","max"){
                margin-bottom: 30px;
            }
        }
        &__list {
            display: flex;
            border-bottom: 2px solid $color-green;
            position: relative;
        }
        &__item {
            &:not(:last-child) {
                border-right: 2px solid $color-green;

                .tabs-nav__link {
                    border-left: 2px solid $color-white;
                    &.active {
                        border-left: 2px solid $color-green;
                    }
                }
            }
            &:last-child {
                .tabs-nav__link {
                    border-right: 2px solid $color-white;
                    &.active {
                        border-right: 2px solid $color-green;
                    }
                }
            }
        }
        &__link {
            font-size: 2.9rem;
            font-weight: 700;
            color: $color-green;
            text-decoration: none;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 12px 5px;
            width: 140px;
            max-width: 100%;
            background-color: $color-white;
            border-top: 2px solid $color-white;
            position: relative;
            transition: all 0.25s ease;
          
            @include mq("tablet","max"){
                font-size: 2.2rem;
                padding: 8px 5px;
                width: 120px;
            }
            &::after {
                position: absolute;
                content: "";
                background-color: $color-white;
                width: 100%;
                height: 2px;
                bottom: -2px;
                left: 0;
                opacity: 0;
            }
            &.active {
                border-top: 2px solid $color-green;
                &::after {
                    opacity: 1;
                }
            }
            &:hover{
                opacity: 0.7;
                @include mq("tablet","max"){
                    opacity: 1;
                }
            }
        }
    }
    &-content {
        display: none;
        &.active {
            display: block;
        }
    }
}
