.hamburger {
    // display: none;
    position: absolute;
    width: 30px;
    height: 30px;
    opacity: 0;
    visibility: hidden;
    right: 0;
    top: 48%;
    transition: all 0.35s ease-in-out;
    z-index: 2;
    cursor: pointer;
    transform: translateY(-50%);

    @include mq("tablet", "max") {
        opacity: 1;
        visibility: visible;
        display: block;
        width: 30px;
        height: 30px;
        position: relative;
        right: unset;
        top: unset;
        transform: none;
    }

    &-icon {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;

        &__left,
        &__right {
            position: absolute;
            height: 4px;
            width: 15px;
            top: 50%;
            transform: translateY(-50%);
            transition: background-color 0.35s ease-in-out;
            background-color: $color-black;

            &::before,
            &::after {
                content: "";
                background-color: $color-black;
                position: absolute;
                width: 15px;
                height: 4px;
                transition: 0.35s;
            }

            &::before {
                top: -10px;
            }

            &::after {
                top: 10px;
            }
        }

        &__right {
            left: 15px;
        }
    }

    &.is-opened {
        .hamburger {
            &-icon {

                &__left,
                &__right {
                    background-color: transparent;
                }

                &__left {
                    &::before {
                        transform: rotateZ(45deg) scaleX(1.4) translate(2px, 2px);
                    }

                    &::after {
                        transform: rotateZ(-45deg) scaleX(1.4) translate(2px, -2px);
                    }
                }

                &__right {
                    &::before {
                        transform: rotateZ(-45deg) scaleX(1.4) translate(-2px, 2px);
                    }

                    &::after {
                        transform: rotateZ(45deg) scaleX(1.4) translate(-2px, -2px);
                    }
                }
            }
        }

        & + .nav {
            opacity: 1;
            visibility: visible;
            right: 0;
        }
    }
}