.bg-gradient {
    &--right {
        background-image: linear-gradient(to right, rgba(44, 176, 143, 0.8), rgba(255, 255, 230, 0.8));
    }

    &--left {
        background-image: linear-gradient(to right, rgba(255, 255, 230, 0.8), rgba(44, 176, 143, 0.8));
    }

    &--page {
        // background: linear-gradient(151deg, rgba(255, 255, 230, 0.8) 50.8%, rgba(44, 176, 143, 0.8) 100%);
        background-image: linear-gradient(118.9deg, rgba(255, 255, 230, 0.8) 45.2%, rgba(44, 176, 143, 0.8) 105%)
    }
}