.contact {
    &__desc {
        font-size: 1.6rem;
        font-weight: 600;
        line-height: calc(21 / 16);
        letter-spacing: 0.03em;

        @include mq("phablet", max) {
            font-size: 1.4rem;
        }
    }

    &__wpr {
        padding: 85px 0 135px;
        @include mq("phablet", max) {
            padding: 60px 0;
        }
    }

    &-form {
        &-wpr {
            margin-top: 25px;
        }

        &__row {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            margin-bottom: 10px;

            @include mq("phablet", max) {
                flex-direction: column;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }

        &__col {
            &-select {
                position: relative;

                &::after {
                    content: "";
                    pointer-events: none;
                    position: absolute;
                    width: 0;
                    height: 0;
                    border-left: 6px solid transparent;
                    border-right: 6px solid transparent;
                    border-top: 12px solid $color-green;
                    right: 10px;
                    top: 50%;
                    transform: translateY(-50%);
                }
            }
        }

        &__ttl {
            max-width: 200px;
            width: 100%;
        }

        &__field {
            max-width: 790px;
            width: 100%;
        }

        &__label {
            font-size: 1.6rem;
            font-weight: 600;
            letter-spacing: 0.03em;
            line-height: 2.2;

            @include mq("phablet", max) {
                font-size: 1.4rem;
            }
        }

        .input-field {
            width: 100%;
            padding: 7px 15px;
            font-family: $font-primary;
            font-size: 1.6rem;
            font-weight: 600;
            letter-spacing: 0.03em;
            border-radius: 0;
            border: 0;
            outline: none;

            @include mq("phablet", max) {
                font-size: 1.4rem;
            }

            &.-textarea {
                resize: none;
                height: 176px;
            }

            &.-select {
                appearance: none;
                -webkit-appearance: none;
                -moz-appearance: none;
                color: $color-black;
                background-color: $color-white;
                cursor: pointer;
                option {    
                    font-size: 1.6rem;
                    font-weight: 600;
                    letter-spacing: 0.03em;
                }
            }
        }

        &__btn {
            margin-top: 25px;
            display: flex;
            gap: 15px;
            margin-left: 200px;
            @include mq("phablet","max"){
                margin-left: 0;
            }
            br{
                display: none;
            }
        }
    }
}
.mw_wp_form_confirm {
    .contact-form__row{
        align-items: center;
        @include mq("phablet","max"){
            align-items: flex-start;
        }
    }
    .contact-form__row:last-of-type{
        align-items: baseline;
    }
    .contact-form__col-select::after{
        content: none;
    }
    .contact-form__field{
        font-weight: 600;
        padding: 8px 15px;
        background-color: $color-white;
    }
    .contact-form__btn{
        margin-top: 35px;
        @include mq("phablet","max"){
            justify-content: center;
        }
    }
}
.mw_wp_form_confirm,
.mw_wp_form_complete{
    .contact__desc{
        display: none;
    }
}
.complete-text{
    font-size: 1.6rem;
    font-weight: 600;
    text-align: center;
    line-height: 1.6;
    margin-bottom: 50px;
}
.mw_wp_form_complete{
    .contact-form__btn{
        margin-left: 0;
    }
}