.btn {
    &-primary {
        font-family: $font-sec;
        display: block;
        padding: 9px 0 10px;
        background-color: $color-white;
        max-width: 400px;
        width: 100%;
        margin: 0 auto;
        border: 1px solid $color-green;
        border-radius: 40px;
        color: $color-green;
        text-decoration: none;
        text-align: center;
        font-size: 1.6rem;
        font-weight: 900;
        transition: all 0.35s ease-in-out;

        @include mq("tablet", "max") {
            max-width: 280px;
        }

        &:hover {
            background-color: $color-green;
            color: $color-white;
        }
    }

    &-submit {
        font-family: $font-sec;
        display: block;
        max-width: 200px;
        width: 100%;
        font-size: 1.6rem;
        font-weight: 900;
        color: $color-green;
        text-align: center;
        text-decoration: none;
        padding: 4px 0 5px;
        background-color: $color-white;
        border: 1px solid $color-green;
        border-radius: 10px;
        cursor: pointer;
        transition: all 0.35s ease-in-out;

        &:hover {
            background-color: $color-green;
            color: $color-white;
        }
        &.-back{
            background-color: $color-green;
            color: $color-white;
            &:hover{
                background-color: $color-white;
                border: 1px solid $color-green;
                color: $color-green;
            }
        }
    }
}