.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;

    &.header-scroll {
        .header {
            &__wrapper {
                padding: 40px 76px;

                @include mq("desktop", "max") {
                    padding: 20px;
                }

                @include mq("bootstrap-lg", "max") {
                    padding: 20px 10px;
                }

                @include mq("tablet", "max") {
                    padding: 20px;
                }

                &::after {
                    opacity: 1;
                    // transform: scaleX(1);
                }
            }
        }

        .nav {
            opacity: 0;
            visibility: hidden;

            &.is-opened {
                @include mq("desktop", "max") {
                    padding-right: 48px;
                }

                @include mq("tablet", "max") {
                    padding-right: 0;
                }
            }
        }

        .hamburger {
            right: 30px;
            opacity: 1;
            visibility: visible;

            @include mq("desktop", "max") {
                right: 20px;
            }

            @include mq("bootstrap-lg", "max") {
                right: 10px;
            }

            @include mq("tablet", "max") {
                right: unset;
            }
        }
    }

    &__wrapper {
        padding: 66px 76px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 10px;
        position: relative;
        transition: all 0.35s ease-in-out;

        @include mq("desktop", "max") {
            padding: 20px;
        }

        @include mq("bootstrap-lg", "max") {
            padding: 20px 10px;
        }

        @include mq("tablet", "max") {
            padding: 20px;
        }

        &::after {
            content: "";
            display: block;
            background-image: linear-gradient(to right, rgba(255, 255, 230), rgba(44, 176, 143));
            position: absolute;
            inset: 0;
            z-index: -1;
            opacity: 0;
            transition: all 0.35s ease;

            @include mq("tablet", "max") {
                content: none;
            }
        }
    }

    &__link {
        display: block;
        transition: opacity 0.35s ease-in-out;

        &:hover {
            opacity: 0.7;

            @include mq("tablet", "max") {
                opacity: 1;
            }
        }
    }
}