.news-detail {
    .news {
        &-tagwpr {
            display: flex;
            justify-content: space-between;
            width: 100%;
            margin-bottom: 32px;
            @include mq("tablet", "max") {
                margin-bottom: 20px;
            }
        }
        &-tag {
            font-size: 2.4rem;
            @include mq("tablet", "max") {
                font-size: 2rem;
            }
        }
        &-date {
            font-size: 2rem;
            @include mq("tablet", "max") {
                font-size: 1.8rem;
            }
        }
    }
    &-inner {
        margin-bottom: 140px;
        @include mq("tablet", "max") {
            margin-bottom: 70px;
        }
    }
    &-img{
        max-width: 560px;
        margin: 0 auto 45px;
        @include mq("tablet","max"){
            margin: 0 auto 25px;
        }
    }
}
