.heading-primary {
    font-size: 11.1rem;
    font-weight: 700;
    -webkit-text-stroke: 1px $color-black;
    -webkit-text-fill-color: transparent;
    text-transform: uppercase;
    /* fallback */
    color: $color-black;
    letter-spacing: 0.03em;
    position: relative;
    padding-left: 32px;
    z-index: 1;
    margin-bottom: 28px;

    @include mq("tablet", "max") {
        font-size: 5rem;
        padding-left: 20px;
        margin-bottom: 18px;
    }

    &::before {
        content: "";
        position: absolute;
        background: $color-white;
        mask-image: url(../img/icons/logo-white.svg);
        mask-repeat: no-repeat;
        mask-size: cover;
        width: 65px;
        height: 120px;
        left: 0;
        top: 58%;
        transform: translateY(-50%);
        z-index: -1;

        @include mq("tablet", "max") {
            width: 30px;
            height: 56px;
        }
    }

    &.-jp {
        font-size: 4.5rem;
        color: $color-green;
        -webkit-text-stroke: unset;
        -webkit-text-fill-color: unset;
        margin-bottom: 57px;

        @include mq("tablet", "max") {
            font-size: 2.6rem;
            margin-bottom: 40px;
            padding-left: 15px;

            &::before {
                width: 30px;
                height: 56px;
            }
        }
    }
}


