.section-feature {
    padding: 90px 0 98px;
    overflow: hidden;

    @include mq("tablet", "max") {
        padding: 60px 0 90px;
    }

    &__wrapper {}

    &-swiper {
        margin-bottom: 40px;

        @include mq("tablet", "max") {
            margin-bottom: 40px;
        }

        &__card {
            background-color: $color-white;
            height: 100%;
            display: flex;

            @include mq("tablet", "max") {
                flex-direction: column;
                justify-content: space-between;
            }
        }

        &__left {
            padding: 25px 32px 25px 38px;

            @include mq("bootstrap-lg", "max") {
                padding: 20px;
            }

            @include mq("tablet", "max") {
                padding: 10px;
            }
        }

        &__heading {
            display: flex;
            align-items: flex-start;
            margin-bottom: 52px;

            @include mq("tablet", "max") {
                // display: block;
                gap: 10px;
                margin-bottom: 30px;
            }
        }

        &__number {
            flex-shrink: 0;
            display: flex;
            align-items: center;
            font-size: 11.346rem;
            font-weight: 700;
            letter-spacing: 0.03em;
            line-height: 0.6;
            -webkit-text-fill-color: transparent;
            -webkit-text-stroke-width: 1px;
            -webkit-text-stroke-color: $color-black;
            @include mq("desktop", "max") {
                font-size: 9rem;
            }

            @include mq("bootstrap-lg", "max") {
                font-size: 7.5rem;
            }

            @include mq("tablet", "max") {
                font-size: 6rem;
            }

            &::after {
                flex-shrink: 0;
                content: "";
                display: block;
                width: 25px;
                height: 76px;
                background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg id='Layer_2' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 25.74 77.1'%3E%3Cdefs%3E%3Cstyle%3E .cls-1 %7B fill: none; stroke: %232cad74; stroke-miterlimit: 10; stroke-width: .86px; %7D %3C/style%3E%3C/defs%3E%3Cg id='text'%3E%3Cline class='cls-1' x1='25.33' y1='.13' x2='.41' y2='76.96'/%3E%3C/g%3E%3C/svg%3E");
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                transform: translateY(12px);

                @include mq("desktop", "max") {}

                @include mq("bootstrap-lg", "max") {
                    height: 58px;
                }

                @include mq("tablet", "max") {
                    transform: translateY(8px);
                    height: 50px;
                    // content: none;
                }
            }
        }

        &__title {
            padding-top: 5px;
            margin-left: 26px;
            font-family: $font-sec;
            color: $color-green;
            font-size: 2.4rem;
            font-weight: 900;
            letter-spacing: 0.03em;
            line-height: 1.4175;

            @include mq("desktop", "max") {
                font-size: 2rem;
                margin-left: 20px;
            }

            @include mq("bootstrap-lg", "max") {
                font-size: 1.8rem;
                margin-left: 16px;
            }

            @include mq("tablet", "max") {
                font-size: 1.6rem;
                margin-left: 0;
            }
        }

        &__desc {
            font-size: 1.8rem;
            letter-spacing: 0.03em;
            line-height: 1.89;
            font-weight: 500;
            @include mq("tablet", "max") {
                font-size: 1.4rem;
            }
        }

        &__right {
            flex: 0 0 calc((486 / 1000) * 100%);
            display: flex;
            justify-content: center;
            align-items: center;

            @include mq("tablet", "max") {
                flex: 0 0 auto;
            }

            img {
                aspect-ratio: 1 / 1;
                object-fit: cover;

                @include mq("tablet", "max") {
                    width: 100%;
                    height: 100%;
                }
            }
        }

        &.swiper {
            @include mq("tablet", "max") {
                overflow: visible;
            }
        }

        .swiper {
            &-wrapper {}

            &-slide {
                height: auto;
            }

            &-dots {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 10px;
                position: absolute;
                bottom: 12px;
                left: 0;
                z-index: 1;

                @include mq("tablet", "max") {
                    position: static;
                    margin-top: 20px;
                }

                .swiper-pagination-bullet {
                    margin: 0;
                    width: 14px;
                    height: 14px;
                    border-radius: 50%;
                    background-color: $color-green;
                    opacity: 0.5;
                    transition: opacity 0.35s ease-in-out;

                    &-active {
                        opacity: 1;
                    }
                }
            }
        }
    }

    &-content {
        &__block {
            background-color: $color-white;
            padding: 36px 36px 45px;

            @include mq("tablet", "max") {
                padding: 20px;
            }

            &:not(:last-of-type) {
                margin-bottom: 20px;

                @include mq("tablet", "max") {
                    margin-bottom: 20px;
                }
            }
        }

        &__title {
            font-family: $font-sec;
            color: $color-green;
            font-size: 2.4rem;
            font-weight: 900;
            letter-spacing: 0.03em;
            margin-bottom: 32px;
        }

        &__list {
            flex-shrink: 0;
            flex: 0 0 calc((100% / 3) - ((60px * 2) / 3));

            &:not(:last-of-type) {
                @include mq("tablet", "max") {
                    margin-bottom: 24px;
                }
            }

            &-wrapper {
                display: flex;
                gap: 60px;

                @include mq("tablet", "max") {
                    display: block;
                }
            }
        }

        &__point {
            font-size: 2rem;
            letter-spacing: 0.03em;
            font-weight: 700;
            position: relative;
            padding-left: 14px;

            &:not(:last-of-type) {
                margin-bottom: 20px;

                @include mq("tablet", "max") {
                    margin-bottom: 12px;
                }
            }

            &::before {
                content: "";
                display: block;
                width: 4px;
                height: 4px;
                border-radius: 50%;
                background-color: $color-black;
                position: absolute;
                top: 11px;
                left: 0;
            }
        }
    }
}

.section-case {
    padding: 67px 0 118px;

    @include mq("tablet", "max") {
        padding: 60px 0 90px;
    }

    &__wrapper {
        position: relative;
    }

    &__content {}

    &-swiper {
        &__wrapper {
            display: flex;
            align-items: center;
            position: relative;
        }

        &__slide {
            text-decoration: none;
            color: $color-black;
            transition: all 0.25s ease;
            &:hover{
                opacity: 0.7;
                @include mq("tablet","max"){
                    opacity: 1;
                }
            }
        }

        &__image {
            text-align: center;
            margin-bottom: 15px;

            img {
                aspect-ratio: 1 / 1;
                width: 100%;
                // width: 120px;
                // height: 120px;
                object-fit: cover;
                object-position: center;
                border: 2px solid $color-green;
            }
        }

        &__desc {
            &-text {
                font-family: $font-sec;
                text-align: center;
                font-size: 1.3rem;
                font-weight: 600;
                letter-spacing: -0.06em;
                line-height: 1.62;
                margin-bottom: 5px;
            }
        }

        &__gerne {
            font-family: $font-sec;
            text-align: center;
            font-family: $font-sec;
            font-size: 1.133rem;
            font-weight: 500;
            letter-spacing: 0.03em;
            line-height: 1.865;
        }
    }

    .case-swiper {
        // max-width: calc(100% - (21px * 2));
        // flex: 0 0 calc(100% - (21px * 2));
        position: relative;
        margin: 0 61px;

        @include mq("tablet", "max") {
            // margin: 0 22px;
            margin: 0 -15px;
        }

        &__prev,
        &__next {
            flex-shrink: 0;
            position: absolute;
            z-index: 9;
            // top: 50%;
            transform: translateY(-50%);
            top: 40px;
            cursor: pointer;
            transition: opacity 0.35s ease-in-out;
            @include mq("bootstrap-lg", "max") {
                top: 70px;
            }
            @include mq("tablet", "max") {
                position: absolute;
                transform: translateY(-50%);
                z-index: 9;
                top: 50%;
            }

            &:hover {
                opacity: 0.7;
            }

            &::before {
                content: "";
                display: block;
                width: 21px;
                height: 40px;
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
            }
        }

        &__prev {
            left: 0;

            @include mq("tablet", "max") {
                left: 12px;
            }

            &::before {
                background-image: url(../img/icons/arrow-left-green.svg);
            }
        }

        &__next {
            right: 0;

            @include mq("tablet", "max") {
                right: 12px;
            }

            &::before {
                background-image: url(../img/icons/arrow-right-green.svg);
            }
        }

        .swiper {
            &-slide {
                transition: all 0.35s ease-in-out;

                &:not(.swiper-slide-active) {
                    @include mq("tablet", "max") {
                        transform: scale(0.7);
                        transform-origin: center;
                        opacity: 0.5;
                    }
                }
            }
        }
    }

    &__btn {
        margin-top: 60px;
    }
}

.section-price {
    padding: 67px 0 92px;

    @include mq("tablet", "max") {
        padding: 60px 0 90px;
    }

    &__wrapper {}

    &-content {
        background-color: $color-white;
        padding: 26px 20px 46px;

        &__block {
            &:not(:last-of-type) {
                margin-bottom: 52px;
            }
        }

        &__title {
            color: $color-green;
            font-size: 3.6rem;
            font-weight: 700;
            margin-bottom: 50px;
        }

        &__subtitle {
            font-size: 2rem;
            font-weight: 600;
            letter-spacing: 0.03em;
            padding-bottom: 16px;
            border-bottom: 1px solid $color-green;
            margin-bottom: 26px;
        }

        &__inner {
            &:not(:last-of-type) {
                margin-bottom: 36px;
            }

            @include mq("tablet", "max") {
                width: 700px;
            }

            &-wrapper {
                @include mq("tablet", "max") {
                    overflow-x: scroll;
                    padding-bottom: 8px;
                }
            }
        }

        &__body {}

        &__row {
            display: flex;
            padding-bottom: 21px;
            border-bottom: 1px solid $color-green;

            &:not(:last-of-type) {
                margin-bottom: 26px;
            }

            &.-br-left {
                position: relative;

                &::after {
                    content: "";
                    display: block;
                    background-color: $color-green;
                    width: 1px;
                    height: 16px;
                    position: absolute;
                    left: 0;
                    bottom: 0;
                }
            }
        }

        &__text {
            flex-grow: 1;
            text-align: center;
            font-size: 1.5rem;
            font-weight: 600;
            letter-spacing: 0.03em;
            padding: 0 6px;

            &:nth-child(1) {
                flex: 0 0 23.69%;
            }

            &:nth-child(2) {
                flex: 0 0 18.78%;
            }

            &:not(:last-of-type) {
                position: relative;

                &::after {
                    content: "";
                    display: block;
                    background-color: $color-green;
                    width: 1px;
                    height: 16px;
                    position: absolute;
                    right: 0;
                    bottom: -21px;
                }
            }

            span {
                &:first-of-type {
                    margin-right: 28px;
                }
            }
        }

        &__btn {}
    }
}

