.news {
    &-title{
        font-size: 2.4rem;
        font-weight: 700;
        color: $color-black;
        margin-bottom: 45px;
        line-height: 1.25;
        letter-spacing: 0.03em;
        @include mq("tablet", "max") {
            font-size: 2rem;
            margin-bottom: 25px;
        }
    }
    &-list {
        margin-bottom: 120px;
        @include mq("tablet", "max") {
            margin-bottom: 60px;
        }
    }
    &-item {
        border-bottom: solid 1px $color-green;
        &:first-child {
            .news-link {
                padding-top: 0;
            }
        }
    }
    &-link {
        padding: 20px 0;
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        gap: 10px;
        text-decoration: none;
        transition: all 0.35s ease-in-out;
        @include mq("tablet", "max") {
            padding: 18px 0;
            gap: 15px;
        }
        &:hover {
            opacity: 0.7;
        }
    }
    &-tagwpr {
        width: 110px;
        max-width: 100%;
        color: $color-green;
        letter-spacing: 0.03em;
    }
    &-desc {
        width: calc(100% - 120px);
        @include mq("tablet", "max") {
           width: 100%;
        }
    }
    &-tag {
        font-size: 1.6rem;
        font-weight: 700;
        padding-bottom: 2px;
        @include mq("tablet", "max") {
            font-size: 1.4rem;
        }
    }
    &-date {
        font-size: 1.4rem;
        font-weight: 400;
        @include mq("tablet", "max") {
            font-size: 1.2rem;
        }
    }
    &-desc {
        font-size: 1.6rem;
        font-weight: 700;
        color: $color-black;
        letter-spacing: 0.03em;
        @include mq("tablet", "max") {
            font-size: 1.4rem;
        }
    }
    .tabs-nav__link.active{
        pointer-events: none;
    }
}
