.hero {
    padding: 337px 0 242px;
    position: relative;

    @include mq("tablet", "max") {
        padding: 250px 0 200px;
    }

    &__wrapper {
        position: relative;

        &>img {}
    }

    &-content {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);

        &__logo {
            margin-bottom: 42px;

            @include mq("bootstrap-lg", "max") {
                max-width: 280px;
            }

            @include mq("tablet", "max") {
                max-width: 200px;
                margin-bottom: 36px;
            }

            @include mq("phablet", "max") {
                max-width: 160px;
                margin-bottom: 26px;
            }
        }

        &__title {
            font-family: $font-sec;
            font-size: 4.819rem;
            font-weight: 900;
            letter-spacing: -0.03em;
            line-height: 1.4117;

            @include mq("tablet", "max") {
                font-size: 2.6rem;
            }
        }
    }
}