html {
    font-size: 10px;
}

body {
    background: $color-white;
    font-family: $font-primary;
    color: $color-black;
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 1.2;

    &.of-hidden {
        overflow: hidden;
    }

    &.of-hide {
        @include mq("tablet", "max") {
            overflow: hidden;
        }
    }
}

.container {
    display: block;
    max-width: 1030px;
    padding: 0 15px;
    margin: 0 auto;

    @include mq("tablet", "max") {
        max-width: 100%;
    }
}

%reset-ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

// delete later

.test {
    height: 400px;
}

.page {
    padding: 220px 0 98px;

    @include mq("tablet", "max") {
        padding: 110px 0 45px;
    }

    &--single {
        padding: 220px 0 125px;

        @include mq("tablet", "max") {
            padding: 110px 0 65px;
        }
    }

    &-wrapper {
        background-color: $color-white;
        padding: 25px 20px;

        &.-sm {
            padding: 50px 20px 20px;

            @include mq("tablet", "max") {
                padding: 25px 20px;
            }
        }

        &.-lg {
            padding: 105px 20px 255px;

            @include mq("tablet", "max") {
                padding: 65px 20px;
            }
        }
    }

    &-inner {
        max-width: 718px;
        width: 100%;
        margin: 0 auto;
    }
}

.iphone-fix{
    .section-feature-swiper__heading{
        margin-bottom: 16px;
    }
    .section-feature-swiper__number {
        line-height: 1;
    }
    .section-feature-swiper__number::after {
        transform: translateY(-10px);
        @include mq("tablet", "max") {
            transform: translateY(-7px);
        }
    }
    .section-feature-content__point::before{
        top: 8px;
    }
    .heading-primary{
        margin-bottom: 15px;
        &:before{
            top: 42%;
        }
    }
}
