.faq {
    &__wpr {
        &:not(:last-child) {
            margin-bottom: 60px;
            @include mq("tablet", "max") {
              margin-bottom: 30px;
            }
        }
    }
    &__ttl {
        font-size: 4rem;
        font-weight: 700;
        color: $color-green;
        letter-spacing: 0.03em;
        font-family: $font-sec;
        margin-bottom: 22px;
        @include mq("tablet", "max") {
            font-size: 2.4rem;
            margin-bottom: 18px;
        }
    }
    &__inner {
        padding: 12px 20px 28px;
        background-color: $color-white;

        @include mq("tablet", "max") {
            padding: 12px 15px 28px;
        }

        @include mq("phablet", "max") {
            padding: 12px 10px 14px;
        }
    }

    &-list {
        // overflow-y: scroll;
        // &.ht-1000 {
        //     max-height: 1000px;
        // }
        // &::-webkit-scrollbar {
        //     width: 16px;
        //     background-color: $color-green-light;
        // }

        // &::-webkit-scrollbar-thumb {
        //     background-color: $color-green;
        // }

        // &__wpr {
        //     // padding-right: 40px;
        //     // @include mq("tablet", "max") {
        //     //     padding-right: 20px;
        //     // }
        //     // @include mq("phablet", "max") {
        //     //     padding-right: 10px;
        //     // }
        // }

        .faq__accordion:last-of-type {
            margin-bottom: 0;
        }
    }

    &__accordion {
        border-bottom: 2px solid $color-green;
        // padding-bottom: 20px;
        // margin-bottom: 20px;
        // padding: 20px 0;

        @include mq("tablet", "max") {
            // padding-bottom: 15px;
            // margin-bottom: 15px;
            // padding: 15px;
        }

        @include mq("phablet", "max") {
            // padding-bottom: 10px;
            // margin-bottom: 10px;
            // padding: 10px;
        }
    }

    &__qns {
        padding-right: 30px;
        padding-left: 40px;
        cursor: pointer;
        position: relative;
        padding-top: 20px;
        padding-bottom: 20px;
        display: flex;
        align-items: center;
        gap: 14px;
        @include mq("tablet", "max") {
            padding-right: 20px;
            padding-left: 30px;
        }

        @include mq("phablet", "max") {
            padding-right: 15px;
            padding-left: 25px;
        }

        &::after {
            content: "+";
            position: absolute;
            font-size: 3.3rem;
            right: 0;
            top: 9px;

            @include mq("tablet", "max") {
                font-size: 2.8rem;
                top: 14px;
            }

            @include mq("phablet", "max") {
                font-size: 2.3rem;
                top: 18px;
            }
        }

        &.active {
            &::after {
                content: "—";
                top: 5px;

                @include mq("tablet", "max") {
                    top: 9px;
                }

                @include mq("phablet", "max") {
                    top: 15px;
                }
            }
        }

        &-text {
            font-size: 1.8rem;
            font-weight: 600;

            @include mq("tablet", "max") {
                font-size: 1.6rem;
                line-height: 1.6;
            }

            @include mq("phablet", "max") {
                font-size: 1.5rem;
            }
        }

        &::before {
            position: absolute;
            content: "Q.";
            font-size: 2.4rem;
            font-weight: 700;
            -webkit-text-stroke: 1px #000000;
            -webkit-text-fill-color: transparent;
            color: #000000;
            left: 0;
            top: 16px;

            @include mq("tablet", "max") {
                font-size: 2rem;
                // top: -4px;
            }

            @include mq("phablet", "max") {
                font-size: 1.6rem;
                // top: -3px;
                top: 20px;
            }
        }
    }

    &__ans {
        padding-left: 40px;
        padding-right: 30px;
        // margin-top: 20px;
        margin-bottom: 20px;
        display: none;
        position: relative;

        @include mq("tablet", "max") {
            padding-right: 20px;
            padding-left: 30px;
        }

        @include mq("phablet", "max") {
            padding-right: 15px;
            padding-left: 25px;
        }

        &-text {
            font-size: 1.6rem;
            font-weight: 600;
            letter-spacing: 0.03em;

            @include mq("tablet", "max") {
                font-size: 1.4rem;
                line-height: 1.6;
            }
        }

        &::before {
            position: absolute;
            content: "A.";
            font-size: 2.4rem;
            font-weight: 700;
            -webkit-text-stroke: 1px #000000;
            -webkit-text-fill-color: transparent;
            color: #000000;
            left: 0;
            top: -5px;

            @include mq("tablet", "max") {
                font-size: 2rem;
                top: -2px;
            }

            @include mq("phablet", "max") {
                font-size: 1.6rem;
                top: 0;
            }
        }

        &.show {
            display: block;
        }
    }
    &-tags {
        display: flex;
        flex-wrap: wrap;
        gap: 28px 20px;
        margin-bottom: 120px;
        @include mq("tablet", "max") {
            gap: 15px;
            margin-bottom: 50px;
        }
        &__item {
            width: calc((100% / 5) - (20px * 4 / 5));
            @include mq("bootstrap-lg", "max") {
                width: calc((100% / 4) - (20px * 3 / 4));
            }
            @include mq("tablet", "max") {
                width: calc((100% / 3) - (15px * 2 / 3));
            }
            @include mq("phablet", "max") {
                width: calc((100% / 2) - (15px / 2));
            }
        }
        &__link {
            font-size: 2.4rem;
            font-weight: 700;
            color: $color-green;
            letter-spacing: 0.03em;
            font-family: $font-sec;
            text-decoration: none;
            background-color: $color-white;
            border: 1px solid $color-green;
            border-radius: 10px;
            display: block;
            width: 100%;
            text-align: center;
            padding: 5px;
            transition: all 0.25s ease;
            @include mq("tablet", "max") {
                font-size: 1.8rem;
            }
            &:hover {
                opacity: 0.7;
                @include mq("tablet", "max") {
                    opacity: 1;
                }
            }
        }
    }
}
